:root {
  font-size: 62.5%; }

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }
  html.is-locked {
    overflow: hidden; }
    html.is-locked body {
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
      overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box; }

b,
strong {
  font-weight: bold; }

body {
  font-family: "";
  font-weight: 400;
  color: #030C11;
  font-size: 1.6rem;
  height: 100%;
  background: #FFFFFF; }

a {
  text-decoration: none; }

button, select, input, textarea {
  font-family: "";
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
  margin: 0;
  outline-style: none;
  outline-color: transparent;
  box-shadow: none;
  border-radius: 0;
  color: #030C11; }

input {
  cursor: text;
  padding: 0 15px; }

textarea {
  cursor: text;
  resize: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

table {
  width: 100%;
  border-collapse: collapse; }

::-webkit-input-placeholder {
  font-size: 1.6rem; }

::-moz-placeholder {
  font-size: 1.6rem;
  opacity: 1; }

:-moz-placeholder {
  font-size: 1.6rem;
  opacity: 1; }

:-ms-input-placeholder {
  font-size: 1.6rem; }

:focus::-webkit-input-placeholder {
  color: transparent; }

:focus::-moz-placeholder {
  color: transparent; }

:focus:-moz-placeholder {
  color: transparent; }

:focus::-ms-input-placeholder {
  color: transparent; }

/**
 * Basic typography style for copy text
 */
.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.footer {
  background-color: #030C11;
  padding: 80px 0; }

.footer__copyright {
  color: #FFFFFF;
  display: block;
  width: auto;
  margin: 0 auto;
  text-align: center;
  font-size: 14px; }

.link {
  color: #FFFFFF;
  background-color: #E3202A;
  border-radius: 32px; }
  .link:hover, .link:focus {
    background-color: #B6050E; }
  .link--disabled {
    pointer-events: none;
    cursor: default;
    background-color: #888888; }
